import React, { useState } from 'react'
import Container from 'components/Container'
import { getSigner } from 'utils'
import { useDropzone } from 'react-dropzone';
import { CardBody, Button, LinkExternal } from '@pancakeswap-libs/uikit'
// import { useTokenContract } from '../../hooks/useContract'
// import { AutoColumn } from 'components/Column'
// import { Contract } from 'ethers'
import * as XLSX from 'xlsx';
import { RowBetween } from 'components/Row'
import { Contract } from 'ethers'
import { useTranslation } from 'react-i18next'
import useToast from 'hooks/useToast'
import BigNumber from 'bignumber.js';
// import { isAddress } from '../../utils'
import AppBody from '../AppBody'
import { Wrapper, Title, WarnTip } from './styleds'
import Mint_Token_ABI from '../../constants/abis/mintToken.json'
import { useActiveWeb3React } from '../../hooks'
// import caseExcel from '../../assets/case.xlsx'


export default function TokenMint({match}) {
    const { tokenAddress, decimals } = match.params
    const { t } = useTranslation()

  const [fileData, setFileData] = useState<any[]>([]);
  // const [fileName, setFileName] = useState('');

  const onDrop = (acceptedFiles: File[]) => {
    if (acceptedFiles.length > 0) {
      const file = acceptedFiles[0];
      // setFileName(file.name);

      const reader = new FileReader();
      reader.onload = (e) => {
        const data = new Uint8Array(e.target?.result as ArrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });

        // 
        const firstSheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[firstSheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
        const jsonDataFilter = jsonData.filter((item: any) => {
          if (item.length !== 2) {
            return false
          }
          // if(!isAddress(item[0]) ) {
          //   return false
          // }
          // if(!item[1]) {
          //   return false
          // }
          return true
        })
        setFileData(jsonDataFilter);
      };

      reader.readAsArrayBuffer(file);
    }
  };

  const accept = {
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': ['.xlsx'],
    'application/vnd.ms-excel': ['.xls'],
  };

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept,
    maxFiles: 1, // 
  });

    const { toastSuccess, toastError } = useToast()
    const { account, chainId, library } = useActiveWeb3React()
    const [loading, setLoading] = useState(false); 
    // const symbol = useSymbol(tokenAddress)
    // if(symbol) {
    //   setTokenSymbol(symbol)
    // }
    // const handleTokenAddress = useCallback(
    //   (value) => {
    //     const symbol = useSymbol(tokenAddress)
    //     setTokenAddress(value)
    //     setTokenSymbol('0x0913249bE694F0681aBcaa972DC8Cec03814f20b')
    //     setApplication('0x0913249bE694F0681aBcaa972DC8Cec03814f20b')
    //   },
    //   []
    // )
    const handleMint = async () => {
      // console.log(fileData, 'fileData')
      if (!chainId || !library || !account || !tokenAddress || !decimals) return
       setLoading(true)
       try {
         if (fileData.length <= 0) {
           toastError(t('Excel upload error'))
           return
         }
         if(fileData.length > 201) {
           toastError(t('The number of addresses cannot exceed 200'))
           return
         }
         const addressList = fileData.slice(1).map((item: any) => item[0])
         const amountList = fileData.slice(1).map((item: any) => new BigNumber(item[1]).multipliedBy(10 ** decimals).toFixed())
         const contract = new Contract(tokenAddress, Mint_Token_ABI,getSigner(library, account))
         const tx = await contract.batchMint(addressList, amountList)
         // eslint-disable-next-line no-console
        await tx.wait();
        toastSuccess(t('Mint success'))
        setFileData([])
         setLoading(false)
       } catch(err) {
        // console.log(err, 'err')
        //  console.log(tokenAddress, 'tokenAddress')
        toastError(t('Mint failed'))
        setFileData([])
         setLoading(false)
       }
     }
    return (
      <Container>
      <AppBody>
        <Wrapper>
          <Title>{ t('Batch mint') }</Title>
         
          <CardBody>
              <RowBetween>
              <span />
              <LinkExternal style={{ marginBottom: '0.5rem' }} download href='/swap/case.xlsx'></LinkExternal>
            </RowBetween>
              <div {...getRootProps()} style={{ border: '2px dashed #cccccc', padding: '1rem', textAlign: 'center', marginBottom: '1rem' }}>
              <input {...getInputProps()} />
              <p>{t('Drag and drop an Excel file here')}</p>
            </div>
            <div>
        {/* {fileName && <div className='file-name'>: {fileName}</div>} */}
          <div style={{ maxHeight: '20vh', overflowY: 'auto', marginBottom: '1rem' }}>
            {fileData.length > 0 && (
              <table style={{ borderCollapse: 'collapse', width: '100%' }}>
                <thead>
                  <tr>
                    {fileData[0].map((header: any, index: number) => (
                      // eslint-disable-next-line react/no-array-index-key
                      <th key={index} style={{ border: '1px solid #ccc', padding: '8px', textAlign: 'left' }}>{header}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {fileData.slice(1).map((row: any, rowIndex: number) => (
                    // eslint-disable-next-line react/no-array-index-key
                    <tr key={rowIndex}>
                      {row.map((cell: any, cellIndex: number) => (
                        // eslint-disable-next-line react/no-array-index-key
                        <td key={cellIndex} style={{ border: '1px solid #ccc', padding: '8px' }}>{cell.length >= 42 ? `${cell.substring(0, 5)  }...${  cell.substring(cell.length - 5, cell.length)}` : cell}</td>
                      ))}
                    </tr>
                  ))}
                </tbody>
              </table>
            )}
          </div>
            </div>
             <WarnTip>
                { t('The upload file can contain up to 200 records') }<br />
                <LinkExternal style={{ marginBottom: '0.5rem' }} target='_black' href='https://bscscan.com/exportData?type=tokenholders'>{ t('Export bsc chain token holder information') }</LinkExternal>
                <LinkExternal style={{ marginBottom: '0.5rem' }} target='_black' href='https://cn.etherscan.com/exportData?type=tokenholders'>{ t('Export eth chain token holder information') }</LinkExternal>
              </WarnTip>
             {fileData.length <= 0 ? (
                <Button disabled width="100%">{ t('Please upload the excel file first') }</Button>
              ) : (
                <Button width="100%" disabled={ loading } onClick={ handleMint }>{ loading ? `${t('Batch mint')}...` : t('Batch mint')}</Button>
              )}
             
          </CardBody>
        </Wrapper>
      </AppBody>
    </Container>
    )
  }