import React, { useState } from 'react'
import Container from 'components/Container'
import InputString from 'components/InputString'
import { Button } from '@pancakeswap-libs/uikit'
import { Link as HistoryLink } from 'react-router-dom'
import { RowBetween } from 'components/Row'
import { ColumnCenter } from 'components/Column'
import { useMemeList } from 'data/MemeList'
import copy from 'copy-to-clipboard';
import { useTranslation } from 'react-i18next'
import useToast from 'hooks/useToast'
import InputNumber from 'components/InputNumber'
// import { useActiveWeb3React } from '../../hooks'
import AppBody from '../AppBody'
import { Wrapper, Name, Desc, CopyIcon, NoMore } from './styleds'
import copyImg from '../../assets/images/copy.png'

export default function MemeList() {
   const { toastSuccess } = useToast()
   const [ page, setPage ] = useState(1)
   const [ goPage, setGoPage ] = useState(page)
   const [memeAddress, setMemeAddress] = useState('')
   const  { tokenList, totalPage } = useMemeList(page, memeAddress)

   const { t } = useTranslation()
   
   const handleInput = (value) => {
      setMemeAddress(value)
   }

   const handleCopy = (address) => {
      copy(address)
      toastSuccess(t('copy successfully'))
    }
     const handleGoPage = (value) => {
        setGoPage(value)
      }
      const switchPage = () => {
      setPage(goPage)
      }
      const reducePage = () => {
      setPage(page - 1)
      }
      const addPage = () => {
      setPage(page + 1)
   }
  return (
  <Container>
   <ColumnCenter style={{ marginBottom: '1rem' }}>
      <InputString value={ memeAddress } placeholder={ t('Please enter MEME address') } onUserInput={ handleInput }/>
   </ColumnCenter>
   <ColumnCenter style={{ minHeight: '63vh', width: "100%" }}>
     { tokenList.length > 0 ? (tokenList.map((item:any) => (
  <AppBody key={ item.tokenAddress }>
    <Wrapper>
       <RowBetween marginBottom='20px'>
         <div>
            <Name>{ item.symbol }</Name>
            <Desc>{ t('Symbol') }</Desc>
         </div>
         <div>
            <Name>{ item.amount }</Name>
            <Desc>{ t('Token amount') }</Desc>
         </div>
         <div>
            <Name className='active'>{ t('Success') }</Name>
            <Desc>{ t('Status') }</Desc>
         </div>
       </RowBetween>
       <Name style={{ textAlign: 'left' }}>{ t('Contract address') }</Name>
       <RowBetween>
         <Desc className='address'>{ item.tokenAddress }</Desc>
         <CopyIcon src={ copyImg } size='15px' onClick={(event) => {
                      event.stopPropagation()
                       handleCopy(item.tokenAddress)
                    }}/>
       </RowBetween>
       <RowBetween style={{ marginTop: '1rem' }}>
         <HistoryLink to={ `/register/${item.lockAdderss}` }>
             <Button width="100%">{ t('Contract Registration') }</Button>
         </HistoryLink>
        <HistoryLink to={ `/airdrop/${item.lockAdderss}` }>
             <Button width="100%">{ t('Airdrop') }</Button>
         </HistoryLink>
       </RowBetween>
    </Wrapper>
  </AppBody>))) : <NoMore>{ t('No records') }</NoMore>}
   </ColumnCenter>
  
   <RowBetween marginTop='20px' marginBottom='15px'>
             <Button style={{ fontSize: '13px', padding: 0, borderRadius: '10px' }} width="80px" height='30px' onClick={ reducePage } disabled={ page <= 1 }>{ t('Prev') }</Button>
             <div style={{ width: '80px', height: '30px' }}><InputNumber style={{ height: '30px', borderRadius: '10px' }} value={ goPage } onUserInput={ handleGoPage }/></div>
             <Button style={{ fontSize: '13px', padding: 0, borderRadius: '10px' }} width="40px" height='30px' onClick={ switchPage }>GO</Button>
             <Button style={{ fontSize: '13px', padding: 0, borderRadius: '10px' }} width="80px" height='30px' disabled={ page >= totalPage } onClick={ addPage }>{ t('Next') }</Button>
           </RowBetween>
 </Container>
)}