// import { useMemo } from 'react'
import { BigNumber } from '@ethersproject/bignumber'
import { AddressZero } from '@ethersproject/constants'
import { utils } from 'ethers'
// import dateFormat from 'dateformat'
import { useLockFactoryContract } from '../hooks/useContract'
import { useSingleCallResult } from '../state/multicall/hooks'

const limit = 6;

export function useMemeList(page: number, memeAddress: string): any {
  const contract = useLockFactoryContract(false)
  const tokenList = useSingleCallResult(contract, 'getMemeList', [page, limit]).result
  let searchList:any = []
  const searchResult = useSingleCallResult(
  utils.isAddress(memeAddress) ? contract : null,
  'searchMemeInfo',
  utils.isAddress(memeAddress) ? [memeAddress] : undefined
);

if (searchResult && searchResult.result) {
  // console.log(searchResult.result, 'searchResult')
  searchList = searchResult.result.map((item:any) => {
    return {
      amount: BigNumber.from(item.initialAmount.toString()).div(`${10 ** 18}`).toString(),
      symbol: item.symbol,
      tokenAddress: item.memeAddress,
      lockAdderss: item.lockAdderss,
    }
  })
}

  const tokens = tokenList?.list || []
  const total = Number(tokenList?.total.toString()) || 0
  const tokensView = tokens.map((item:any) => {
    return {
      amount: BigNumber.from(item.initialAmount.toString()).div(`${10 ** 18}`).toString(),
      symbol: item.symbol,
      tokenAddress: item.memeAddress,
      lockAdderss: item.lockAdderss,
    }
  })
   const tokenTrueList = tokensView.filter((item) => {
      return item.tokenAddress !== AddressZero
    })
  return {
    tokenList: searchList.length > 0 ? searchList: tokenTrueList,
    totalPage: Math.ceil(total / limit)
  }
}

export default useMemeList
